import React,{useState, useContext, useEffect, useRef} from 'react'
import {Form} from 'react-bootstrap';
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import styles2 from '../css/COLUMN.module.css';
import {TextField} from '@material-ui/core';
import styles from '../css/Captcha.module.css';
import Revision from '../../Icons/Revision';
import Revision2 from '../../Icons/Revision2';
import axios from '../../axios';
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { handleDisplayProperty } from '../../components/IFormDesignerLayout/IFormDesignerLogic';
import { GetState, GetStateFromData, useElementWidth } from '../../components/CustomHooks/CustomHooks';
import EditableLabel from '../../ReusableComponents/EditableLabel/EditableLabel';

function Captcha(props){
  const isSubformInPage = props?.isSubformInPage || false;

  const contexts = GetStateFromData(props.containerId);
  const {tempCanWidth, controlSelected}  = GetState("tempCanWidth","controlSelected");

  const {updateJson, IdPrecedence, deleteControl, formState2,quickToolsClose, device, modeReadOrWrite, openQuickToolFor }=contexts.contexts;

  const { updatedComponentInFormState, removeAndUpdateComponentInFormState} =updateJson; 

    const data = props.data;
    const actualControlId = props.data.id;
    let mappingId = props.data["style"] && props.data["style"]["MappingId"];
    let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
    let suggestedId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

    const [mobileDisplay, setMobileDisplay] = useState(true);
    const [tabDisplay, setTabDisplay] = useState(true);
    const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false
    const [open, setOpen] =  useState(conditionalQuickToolOpening);

	
    const startMobile = useRef();
    const endMobile= useRef();
    const startTab= useRef();
    const endTab= useRef();
    const [defaultLabel, setDefaultLabel] = useState(
      ((props.data.style && props.data.style.Label && props.data.style["Label"]) || props.data.style["Label"] === "")
      ? props.data.style["Label"]
      : props.data.id
    );
    const [editLabel, setEditLabel] = useState(false);

    useEffect(() => {
        if (formState2 && formState2.style) {
            startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
            endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
            startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
            endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
        }
    },[formState2.style?.["Breakpoint"], formState2.style?.["Breakpoint"]?.["Mobile"], formState2.style?.["Breakpoint"]?.["Tab"]]); 

    useEffect(() => {
      if(props?.previewmode)
      handleDisplayProperty(props, device, setMobileDisplay, setTabDisplay, startMobile.current, endMobile.current, startTab.current, endTab.current)
    },[props?.data?.style?.["MobileVisible"], props?.data?.style?.["TabsVisible"], device, endMobile, endTab, startMobile, startTab,tempCanWidth]);
	

    var inputStyle={  
        style:{        
            height: "24pt",
            padding:"4.5px !important",
            "word-break": "break-word"
        },
        
        };
    
        const [imgVar, setImgVar]=useState();

        useEffect(()=>{
                if(imgVar === undefined && props.previewmode){
                     
                  var jsonToSend = {};
                  var remainingURL = "/viewform/getCaptcha";      
                  axios
                    .post(remainingURL, jsonToSend, { withCredentials: true })
                    .then((response) => {

                      setImgVar(response.data["CAPTCHA_Image"]);
                    
                    });

                } 
        },[])

        useEffect(() => {
          if (
              ((props.data.style &&
              props.data.style.Label &&
              props.data.style["Label"]) || props?.data?.style?.["Label"] === "")
          ) {
              setDefaultLabel(props.data.style["Label"]);
            }
          }, [props.data.style["Label"]]);

        

      
        const [countCaptcha, setCountCaptcha]=useState(0);

        var setCount;

        if(props.data && props.data.style && props.data.style['SetRetryCount']){
         setCount = props.data && props.data.style && props.data.style['SetRetryCount'];
        }else{
          setCount=3;
        }

        const bringEditableLabel = (event) => {
          setEditLabel(true);
        };

        function getCaptcha(){

          var capCount=0;

          var jsonToSend = {};
          var remainingURL = "/viewform/getCaptcha";

          //  console.log("Captcha ");
      
          axios
            .post(remainingURL, jsonToSend, { withCredentials: true })
            .then((response) => {
              capCount=countCaptcha+1;
              setCountCaptcha(capCount)
              // console.log("Captcha ", response.data);
              // console.log("Captcha ", response.data["CAPTCHA_Image"]);
              setImgVar(response.data["CAPTCHA_Image"]);
            });
            
        }

        var font_color = props.data.style && props.data.style["FontColor"];
        if(font_color!==undefined && font_color!==null && font_color!==''){
    
        } else {
            font_color="#000000";
        }
    
        var font_family = props.data.style && props.data.style["FontFamily"];
        if(font_family !==undefined && font_family !==null && font_family !==''){
    
        } else {
            font_family="Open Sans";
        }
    
    
        var font_weight = props.data.style && props.data.style["FontWeight"];
        if(font_weight !==undefined && font_weight !==null && font_weight !==''){
    
        } else {
             font_weight="normal";
        }
    
    
        var font_style = props.data.style && props.data.style["FontStyle"];
    
        if(font_style!==undefined && font_style!==null && font_style!==''){
        } else {
          font_style="normal";
        }

        var font_size = props.data.style && props.data.style["FontSize"];

        var inputStyleLabel={         
          color : font_color,
          fontSize: font_size,
          fontFamily: font_family,
          fontWeight: font_weight,
          fontStyle: font_style,
          lineHeight: font_size,
          "word-break": "break-word",
          "white-space":"break-spaces"
  }

  var isEnable;
      isEnable = true
      if(props.previewmode && modeReadOrWrite === "r"){
          props.data.style.Enable = "false"
          isEnable = false
      }
      
      const setLabelHandler = (recievedLabel) => {
        try {
          if (props.data.style && props.data.style.Label) {
            setDefaultLabel((prevDefaultLabel) => {
              if (prevDefaultLabel === recievedLabel) {
                return prevDefaultLabel;
              } else {
                props.data.style["Label"] = recievedLabel;
                return recievedLabel;
              }
            });
            setEditLabel(false);
          }
        } catch (err) {
          console.error(err);
        }
      };

var revisionColor="#0072c6";
var disabledrevisonColor="#b2deff";
const [captchaValue,setCaptchaValue]=useState("");




    



        var padding_left = props.data.style && props.data.style["SpacingLeft"];
        if(padding_left !==undefined && padding_left !==null && padding_left !==''){

        } else {
            padding_left="0px";
        }

        var padding_right = props.data.style && props.data.style["SpacingRight"];
        if(padding_right !==undefined && padding_right !==null && padding_right !==''){

        } else {
            padding_right="0px";
        }

        var padding_top = props.data.style && props.data.style["SpacingTop"];
        if(padding_top !==undefined && padding_top !==null && padding_top !==''){

        } else {
            padding_top="0px";
        }

        var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
        if(padding_bottom !==undefined && padding_bottom !==null && padding_bottom !==''){

        } else {
            padding_bottom="0px";
        }


        var inputStylePadding={  
        paddingLeft : padding_left,
        paddingRight : padding_right,
        paddingTop : padding_top,
        paddingBottom : padding_bottom,
        display:"block"
        }
// Enter the text shown in the image
//  console.log("Captcha style", props.data.style)
    if(props.previewmode){
        return (
            <Form.Group
            className={styles2.formStyle + ' spacingThemeControls '}
             style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : inputStylePadding}
             id={suggestedId+'_group'}
             controlId="dob"  
             data-placement="top" 
             control_id={data.id} 
              key={data.id}
              elem_type='captcha'
              >
                <Form.Label  style={inputStyleLabel}
                   className={' spacingThemeLabel ' + 'controlFontSize' }
                >
                {(props.data && props.data.style && props.data.style['Label']) ? props.data.style['Label'] : suggestedId}
            </Form.Label> 
               <div className={styles.captchaBox} >
                 <div className={styles.textDivBox}>
                   <input aria-label={"Captcha Value"} id={suggestedId} className={styles.inputBox}
                   placeholder={props.data && props.data.style && props.data.style['Placeholder']} 
                   value={captchaValue}
                   onChange={(e)=>setCaptchaValue(e.target.value)}
                   disabled={!isEnable}
                   >

                   </input>
                   </div>
                 <div className={styles.codeDivBox}>
                     {imgVar !== undefined ?
                     <>
                     <img src={"data:image/png;base64," +  imgVar} className={styles.imageClass} alt='Img Captcha'/>
                     </>
                     : null}
                    </div>
                 <div className={styles.refreshDivBox}>
                   {countCaptcha < setCount ?
                   <>
                   <div className={styles.refreshIcon} style={{pointerEvents: isEnable === false ? "none" : "initial"}} onClick={()=>getCaptcha()} color={revisionColor} ><Revision /></div>
                   </>
                   :  
                   <>
                   <div className={styles.refreshIcon} color={disabledrevisonColor} ><Revision2 /></div>
                   </>}

                   </div>
               </div>
            </Form.Group>
       )
    } else {
        return (
            <>
            <Form.Group  onClick={isSubformInPage ? {} : (e)=>props.handleSelected(e,props.id,"n")}
              tabIndex="1" 
              className={styles2.formStyle + ' spacingThemeControls '}
              key={data.id} id={suggestedId+'_group'} control_id={props.id}
               elem_type='captcha' frameindex={data.frameIndex}  elementindex={data.elementIndex}
               style={inputStylePadding}
               onKeyDown={(e) => props.handleSelected(e)}
                     >
                
            <EditableLabel defaultLabel={
            <Form.Label  style={inputStyleLabel}
            onDoubleClick={bringEditableLabel}
            id={suggestedId+'_label'}
            className={' spacingThemeLabel ' + 'controlFontSize' }
            control_id={props.data.id} 
                        elem_type='captcha' 
                        frameindex={data.frameIndex}  
                        elementindex={data.elementIndex}
         >
         {((props.data && props.data.style && props.data.style['Label']) || props?.data?.style?.["Label"] === "") ? props.data.style['Label'] : suggestedId}
     </Form.Label> 
          }
          editlabel={editLabel}
          default_value={defaultLabel}
          setLabelHandler={(recievedLabel)=>setLabelHandler(recievedLabel)}
          />

                <TextField
                disabled={isEnable}
                id={suggestedId}
                fullWidth
                control_id={props.id}
                variant="outlined"
                size="small"
                placeholder={props.data && props.data.style && props.data.style['Placeholder']}
                inputProps={{
                  "aria-label": "Input Value"
                }}
                InputProps={inputStyle}
                />
              {controlSelected?.id === props?.id &&
              <ElemDeleteControl 
                isSubformInPage={isSubformInPage}
              containerId={props.containerId}
             id={props.data.id} 
             deleteControl={deleteControl} 
             elemType={props.data.controlName}//changes made by asloob because settings were not visible in case of captcha
             setDragging={props.setDragging}
             handleReorderStart={props.handleReorderStart}
             handleReorderDrop={props.handleReorderDrop}
             data={props.data} 
             dragId={props.dragId}
             dragType={'column'}
                /> 
              }
 
            </Form.Group> 

            </>
       )
    }

}

export default Captcha